.example{font-family:-apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,Oxygen,Ubuntu,Cantarell,"Open Sans","Helvetica Neue",sans-serif;line-height:1.5;background-color:#fff;color:#2f2f37;max-width:960px;padding:16px}.example code{font-family:monospace;font-size:16px;font-weight:700;background-color:#ff6b0a;color:#fff;padding:2px 6px;border-radius:2px}.example .background{height:240px;background-image:url(../images/logo.png);background-size:cover;background-position:center}.topBar{display:flex;align-items:center;justify-content:center;height:60px;gap:0;margin:0;background-color:#0062ff;box-shadow:rgba(0,0,0,.35) 0 5px 15px}.formField{position:relative;display:flex}.search-imput{width:100%;padding:10px;height:45px;outline:0;border:1px solid rgba(33,33,33,.2);border-radius:20px 0 0 20px;border-color:250ms cubic-bezier(.4,0,.2,1)}.search-button{border:none;height:45px;width:25%;border-radius:0 20px 20px 0;cursor:pointer}.search-button:active{scale:1.05;background-color:#c4c2c2}.icon{display:inline-block;width:10px;height:10px;stroke-width:0;stroke:currentColor;fill:currentColor}